:root{
	font-size: 10px;
}

*,
*:before,
*:after{
	box-sizing: border-box;
}

body{
	overflow-x: hidden;
	margin: 0;
	font-family: 'Montserrat', sans-serif;
	font-size: 2.2rem;
}

a{
	text-decoration: none;
	color: #000;
}

ul{
	padding: 0;
	margin: 0;
}

.button{
	background: none;
	outline: none;
	cursor: pointer;
	border: none;
	transition: .2s linear;
}

.input{
	outline: none;
	border: none;
}

.container{
	max-width: 1600px;
	width: 100%;
	margin: 0 auto;
}

// Главная

.header{
	width: 100%;
	background: linear-gradient(to right, #DDDDDD 55%, #424141 45%);
	padding: 1.5rem 0;
}

.header__inner{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header__nav--inner{
	width: 56%;
	display: flex;
	align-items: center;
}

.header__nav--inner a{
	font-weight: 600;
	margin-right: 9rem;
	transition: .2s linear;
}

.header__nav--inner a:hover{
	color: #959595;
}

.header__nav--inner a:last-child{
	margin-right: 0;
}

.fas.mobile__menu--icon{
	display: none;
}

.header__cart--inner{
	width: 41%;
	display: flex;
	align-items: center;
}

.header__search--inner{
	position: relative;
	margin-right: 6.9rem;
}

.header__search{
	width: 440px;
	border-radius: .3rem;
	background: #4B4B4B;
	font-size: 2.2rem;
	padding: 1rem 4.5rem 1rem 2rem;
	color: #fff;
}

.header__search--icon{
	color: #8C8C8C;
	position: absolute;
	right: 15px;
	top: 50%;
	transform: translateY(-50%);
}

.header__cart--box{
	display: flex;
	align-items: center;
	cursor: pointer;
	color: #fff;
}

.header__cart--box:hover{
	color: #959595;
}

.header__cart--icon{
	margin-right: 1rem;
	transition: .2s linear;
}

.header__cart--text{
	font-weight: 600;
	transition: .2s linear;
}

// Блок лого

.logo{
	width: 100%;
	padding: 1.5rem 0 1rem 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}

.logo__inner{
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.logo__box, .logo__pats--inner{
	display: flex;
	align-items: center;
}

.logo__img--inner{
	width: 295px;
	height: auto;
	margin-right: 2.5rem;
}

.logo__img{
	width: 100%;
	height: auto;
}

.logo__text{
	font-size: 2.4rem;
	max-width: 265px;
	color: #B0B0B0;
	font-weight: 800;
}

.logo__pats--item{
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-right: 10rem;
	transition: .2s linear;
	cursor: pointer;
}

.logo__pats--item:hover{
	opacity: .7;
}

.logo__pats--item:last-child{
	margin-right: 0;
}

.dog__img--box{
	width: 90px;
	height: auto;
}

.cat__img--box{
	width: 70px;
	height: auto;
}

.logo__pats--item--img{
	width: 100%;
	height: auto;
}

.logo__pats--item--text{
	color: rgba(0, 0, 0, 0.7);
	font-weight: 600;
}

// Главная часть

.main{
	background: url("../img/header-bg.png") center no-repeat;
	background-size: cover;
	padding: 12.3rem 0;
}

.main__inner{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.main__suptitle{
	font-size: 4.7rem;
	font-weight: 800;
	color: #fff;
	text-shadow: 0 0 10px #959595;
}

.main__title{
	font-weight: 800;
	font-size: 6.9rem;
	color: #F90;
	margin-top: -1.5rem;
}

.main__subtitle{
	font-size: 3.9rem;
	font-weight: 700;
	color: #fff;
	text-shadow: 0 0 10px #959595;
}

.main__point--inner{
	display: flex;
	align-items: center;
	margin-top: 10rem;
}

.main__button{
	margin-top: 6rem;
	padding: 2.8rem 5rem;
	background: #FF9900;
	color: #fff;
	font-weight: 700;
	font-size: 2.7rem;
}

.main__button:hover{
	background: darken(#FF9900, 8);
}

.main__point--item{
	display: flex;
	align-items: center;
	margin-right: 2rem;
}

.main__point--img--inner{
	width: 70px;
	height: auto;
	margin-right: 1rem;
}

.main__point--img{
	width: 100%;
	height: auto;
}

.main__point--text{
	color: #fff;
	max-width: 160px;
	font-weight: 600;
	text-shadow: 0 0 10px #959595;
}

// Контент главной

.content{
	padding: 6rem 0;
	background: #E5E5E5;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.content__title{
	color: rgba(0, 0, 0, 0.7);
	font-size: 3.2rem;
	font-weight: 800;
	margin-top: 7rem;
}

.content__title:first-child{
	margin-top: 0;
}

.card__inner{
	width: 100%;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	flex-wrap: wrap;
}

.card__item{
	background: #fff;
	display: flex;
	flex-direction: column;
	padding: 2.3rem 2.7rem;
	width: 23%;
	margin: 2rem 2rem 0 0;
}

.card__item--img--inner, .card__item--img{
	width: 100%;
	height: auto;
}

.card__price--inner{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 1rem;
}

.card__price{
	color: #E4B12C;
	font-size: 2.5rem;
	font-weight: 600;
}

.card__size{
	font-size: 2.1rem;
	color: #C2C2C2;
	font-weight: 500;
}

.size__bold{
	font-size: 2.7rem;
	color: #505050;
	font-weight: 500;
}

.card__title{
	margin-top: 1rem;
	color: #000;
	font-weight: 600;
}

.card__rating--inner{
	margin-top: 1rem;
	width: 105px;
	height: auto;
}

.card__desc{
	font-size: 1.4rem;
	margin-top: 1rem;
}



// Предподвалье

.supfooter{
	background: #262626;
	padding: 1.8rem 0 2.8rem 0;
}

.supfooter__inner{
	
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: center;
}

.supfooter__inner a{
	color: #fff;
	font-weight: 600;
	margin: 1rem 1rem 0 0;
}

// Подвал

.footer{
	background: #353535;
	padding: 4.2rem 0 7rem 0;
}

.footer__inner{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.footer__logo--inner{
	width: 295px;
	height: auto;
}

.footer__logo--img{
	width: 100%;
	height: auto;
}

.footer__social--inner{
	display: flex;
	align-items: center;
}

.footer__social--item{
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-right: 5rem;
}

.footer__social--text{
	font-size: 1.4rem;
	font-weight: 600;
}

.footer__social--box{
	width: 48px;
	height: 48px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.footer__phone--inner{
	display: flex;
	align-items: flex-end;
	flex-direction: column;
}

.fab.social__icon{
	color: #fff;
}

.vk{
	color: #4D76A1;
}

.pint{
	color: #CB2027;
}

.inst{
	color: #3F729B;
}

.fb{
	color: #3B5998;
}

.vk__bg{
	background: #4D76A1;
}

.pint__bg{
	background: #CB2027;
}

.inst__bg{
	background: #3F729B;
}

.fb__bg{
	background: #3B5998;
}

.footer__phone--item{
	color: #fff;
	font-weight: 800;
}

.footer__phone--copy{
	margin-top: .5rem;
	color: #959595;
	font-size: 1.5rem;
}





// Мобильные меню

.scroll{
	overflow: hidden;
}

.mobile__menu{
	width: 50vw;
	height: 100vh;
	background: #fff;
	position: fixed;
	z-index: 15;
	top: 0;
	left: 0;
	transform: translateX(-110%);
	box-shadow: 0 0 60px #d7d7d7;
	transition: .6s cubic-bezier(0,.71,1,1);
	padding: 6rem 4.5rem 2rem 4.5rem;
}

.mobile__menu--fade{
	transform: translateX(0%);
}

.mobile__menu--cross{
	font-size: 3.5rem;
	position: absolute;
	top: 20px;
	right: 20px;
	cursor: pointer;
}

.mobile__menu--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.mobile__menu--inner a{
	font-weight: 600;
	margin-top: 1rem;
	font-size: 2rem;
}



// Адаптив

@media(max-width: 1650px){
	.container{
		max-width: 1170px;
	}

	body{
		font-size: 2rem;
	}

	.header__nav--inner a{
		margin-right: 4rem;
	}

	.header__nav--inner{
		width: 42%;
	}

	.header__search--inner{
		margin-right: 3rem;
	}

	.header__search{
		width: 310px;
	}

	.logo__img--inner, .logo__resp{
		width: 245px;
		flex-shrink: 0;
	}

	.dog__img--box{
		width: 80px;
	}

	.cat__img--box{
		width: 63px;
	}

	.logo__text{
		font-size: 2.1rem;
	}

	.main__suptitle{
		font-size: 4.5rem;
	}

	.main__title{
		font-size: 6.4rem;
	}

	.main__subtitle{
		font-size: 3.5rem;
	}

	.main__point--inner{
		margin-top: 9rem;
	}

	.main__button{
		margin-top: 5rem;
		padding: 2.5rem 4.5rem;
		font-size: 2.5rem;
	}

	// Карточка товара
	.card__price{
		font-size: 2rem;
	}

	.card__size{
		font-size: 1.8rem;
	}

	.size__bold{
		font-size: 2rem;
	}
}

@media(max-width: 1200px){
	.container{
		max-width: 970px;
	}

	body{
		font-size: 1.7rem;
	}

	.header__nav--inner a{
		margin-right: 2rem;
	}

	.header__nav--inner{
		width: 42%;
	}

	.header{
		background: linear-gradient(to right, #ddd 50%, #424141 50%);
	}

	.header__cart--inner{
		width: 48%;
	}

	.logo__img--inner, .logo__resp{
		width: 225px;
	}

	.dog__img--box{
		width: 70px;
	}

	.cat__img--box{
		width: 60px;
	}

	.logo__text{
		font-size: 2.1rem;
	}

	.logo__pats--item{
		margin-right: 2rem;
	}

	.footer__social--item{
		margin-right: 2rem;
	}

	// Карточка товара
	.card__item{
		width: 30.3%;
	}
}

@media(max-width: 998px){
	.container{
		max-width: 730px;
	}

	.header__nav--inner a, .logo__pats--inner{
		display: none;
	}

	.fas.mobile__menu--icon{
		display: block;
		font-size: 3rem;
		cursor: pointer;
		transition: .2s linear;
		color: #fff;
	}

	.mobile__menu--icon:hover{
		opacity: .7;
	}

	.header{
		background: #424141;
	}

	.header__nav--inner, .header__cart--inner{
		width: auto;
	}

	.logo__img--inner, .logo__resp{
		width: 200px;
		margin-right: 0;
	}

	.logo__box{
		width: 100%;
		justify-content: space-between;
	}

	.logo__text{
		max-width: 40%;
		font-size: 1.6rem;
	}

	.footer__social--box{
		width: 35px;
		height: 35px;
		font-size: 1.9rem;
	}

	.footer__social--inner{
		order: 3;
		width: 100%;
		justify-content: center;
		margin-top: 2rem;
	}

	.footer__inner{
		flex-wrap: wrap;
	}

	.main__suptitle{
		font-size: 4rem;
	}

	.main__title{
		font-size: 5.5rem;
	}

	.main__subtitle{
		font-size: 3rem;
	}

	.main__point--inner{
		margin-top: 5rem;
	}

	.main__button{
		margin-top: 3rem;
		padding: 2rem 3.5rem;
		font-size: 2.2rem;
	}

	// Карточка товара
	.card__inner{
		justify-content: space-between;
	}
	.card__item{
		width: 47%;
	}

	.card__item:nth-child(even){
		margin-right: 0;
	}
}

@media(max-width: 768px){
	.container{
		max-width: 450px;
	}

	.header__search{
		font-size: 1.7rem;
		width: 200px;
	}

	.header__cart--icon{
		margin-right: .5rem;
	}

	.mobile__menu{
		width: 100vw;
	}

	.footer__inner{
		flex-direction: column;
		align-items: center;
	}

	.footer__phone--inner{
		align-items: center;
		margin-top: 2rem;
	}

	.main__suptitle{
		font-size: 3rem;
	}

	.main__title{
		font-size: 4rem;
	}

	.main__subtitle{
		font-size: 2.3rem;
	}

	.main__point--inner{
		margin-top: 3rem;
		flex-direction: column;
	}

	.main__button{
		padding: 1.4rem 2.5rem;
		font-size: 2rem;
	}

	.main{
		padding: 7rem 0;
	}
	
	// Карточка товара
	.card__item{
		width: 100%;
		margin-right: 0;
	}
}

@media(max-width: 480px){
	.container{
		max-width: 300px;
	}
	
	.logo__box{
		flex-direction: column;
	}

	.logo__text{
		max-width: 100%;
		text-align: center;
	}

	.logo__img--inner, .logo__resp{
		width: 170px;
	}

	.header__search{
		font-size: 1.5rem;
		width: 80px;
		transition: .3s linear;
		padding: 1rem 3.5rem 1rem 1rem;
	}

	.header__search:focus{
		width: 140px;
	}

	.header__search--inner{
		margin-right: 1rem;
	}

	body{
		font-size: 1.5rem;
	}

	.main__suptitle{
		font-size: 2rem;
	}

	.main__title{
		font-size: 2.5rem;
		margin-top: 0;
	}

	.main__subtitle{
		font-size: 1.7rem;
	}

	.main__point--inner{
		margin-top: 2rem;
	}

	.main__button{
		padding: 1.2rem 2rem;
		font-size: 1.7rem;
	}

	.main{
		padding: 4.5rem 0;
	}
}